import { createWebHistory, createRouter } from "vue-router";

import FormPage from '@/views/page/form/Forms.vue';
import Form from '@/views/page/form/Form.vue';
import Checks from '@/views/page/form/Checks.vue';

import ComponentsPage from '@/views/page/components/Components.vue';
import Button from '@/views/page/components/Button.vue';
import Modal from '@/views/page/components/Modal.vue';
import Dropdown from '@/views/page/components/Dropdown.vue';
import Collapse from '@/views/page/components/Collapse.vue';
import Tab from '@/views/page/components/Tab.vue';
import Alerts from '@/views/page/components/Alerts.vue';
import Table from '@/views/page/components/Table.vue';


const routes = [
  {
    path: '/',
    name : 'Design System',
    component :  () => import('../views/DesignSystem.vue'),
  },
  {
    path: '/foundation',
    name : 'Foundation',
    component :  () => import('../views/page/foundation/Foundation.vue'),
    children:[          
      {
        path: 'color',
        name : 'Color',
        component :() => import('../views/page/foundation/Color.vue'),
      },{
        path: 'typography',
        name : 'Typography',
        component : () => import('../views/page/foundation/Typography.vue'),
      },{
        path: 'icon',
        name : 'Icon',
        component : () => import('../views/page/foundation/Icon.vue'),
      },{
        path: 'grid',
        name : 'Grid',
        component : import('../views/page/foundation/Grid.vue'),
      },{
        path: 'breakpoints', 
        name: 'Breakpoints',
        component : import('../views/page/foundation/Breakpoints.vue'),
      }
    ]
  },{
    path: '/form',
    name : 'Form',
    component : FormPage,
    children:[          
      {
        path: 'form',
        name : 'Form',
        component : Form,
      },{
        path: 'checks',
        name : 'Checks & Radios & Swiches',
        component : Checks,
      }
    ]
  },{
    path: '/components',
    name: 'Components',
    component: ComponentsPage,
    children: [
      {
        path: 'button', 
        name: 'Button',
        component: Button,
      },{
        path: 'modal', 
        name: 'Modal',
        component: Modal,
      },{
        path: 'dropdown', 
        name: 'Dropdown',
        component: Dropdown,
      },{
        path: 'collapse', 
        name: 'Collapse',
        component: Collapse,
      },{
        path: 'tab', 
        name: 'Tab',
        component: Tab,
      },{
        path: 'alerts', 
        name: 'Alerts',
        component: Alerts,
      },{
        path: 'table', 
        name: 'Table',
        component: Table,
      }
    ]
  }
];
  
const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router; 